* {
  margin: 0;
  padding: 0;
}

html{
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif ;
  font-style: normal;
  font-size: 62.5%;
}

.header-div {
  display: flex;
  justify-content: space-between;
  padding-top: 2rem;
  margin-bottom: 4rem;
 
}

.company-name {
  font-size: 1.6rem;
  line-height: 2.4rem;
}

.header-links {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 400;
  text-decoration: none;
  margin-left:  2rem;
  cursor: pointer;
}

.hamburger {
  display: none;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-btn {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 500;
  color: #AD4C4C;
  background-color: white;
  border: .1rem solid #AD4C4C;
  border-radius: .5rem;
  padding: .5rem .8rem;
  margin-left:  2rem;
  cursor: pointer;
}


@media screen and (min-width: 76.8rem) {
  html {
    font-size: 100%;
  }

  .hamburger {
    display:block;
  }

  .header-btn {
    padding: .8rem 1.8rem;
  }
}




.home-text p {
  margin-top: 3rem;
  font-size: 1.3rem;
}

.home-btn {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 500;
  color: #AD4C4C;
  background-color: white;
  border: .1rem solid #AD4C4C;
  border-radius: .5rem;
  padding: .8rem 1.8rem;
  margin-top: 4rem;
  cursor: pointer;
}



.input-label {
  font-size: 1.4rem;
  line-height: 2.1rem;
  font-weight: 500;
  display: block;
  margin-bottom: .8rem;
  margin-top: 2rem;
}

.input {
  font-size: 1.2rem;
  line-height: 1.8rem;
  font-weight: 400;
  color: #696969;
  padding: 1rem;
  width: 90%;
  border: .1rem solid #C4C4C4;
  border-radius: .5rem;
}

.preview-btn {
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 600;
  color: white;
  background-color: #885ecc;
  border: 0.1rem solid #885ecc;
  border-radius: 0.5rem;
  padding: 1.3rem auto;
  margin: 3rem 0 ;
  width: 100%;
  height: 5rem;
  cursor: pointer;
  text-align: center;
}

.heading {
  text-align: center;
  margin-top: 1rem;
  color: #000;
}

.input-label-preview {
  color: #885ecc;
  font-size: 1.4rem;
 }

 .preview-div{
  border: 0.3rem solid #885ecc;
  border-radius: 2rem;
  max-width: 30rem;
  margin: auto;
  padding: 2rem;
 }

 .preview-div .heading{
  color: rgb(17, 83, 83);
  margin-bottom: 2rem;
  text-align: center;
 }

 .output {
  font-size: 1.4rem;
  line-height: 1.8rem;
  font-weight: 400;
  color: black;
  padding: 1rem;
  width: 100%;
}

.error {
  color: #f50c0c;
  font-size: 0.8rem;
  display: relative;
  background-color: white;
  padding: 0.3rem;
  margin-top: 5rem ;
}



@media screen and (min-width: 76.8rem) {

  .preview-btn{
    width: 65%;
    display: block;
    margin: 3rem auto;
  }

  /* .package-body{
    display: flex;
    align-items: center;
  } */
}

.error-two {
  color: #f50c0c;
  font-size: 1.8rem;
  text-align: center;
  
  padding: 0.3rem;
  margin: 2rem auto;
}

.error-two > h3, .error-two > p {
  background-color: white;
  display: inline-block;
  padding: .5rem;
}



.login-div{
  border: 0.3rem solid #290661;
  border-radius: 2rem;
  max-width: 30rem;
  margin: auto;
  padding: 2rem;
}
